.toggle-button{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    height:20px;
    width:30px;
    background:transparent;
    border:none;
    cursor: pointer;
    padding:0;
    box-sizing: border-box;
}

.toggle-button:focus{
    outline:none;
}
.toggle-button__line {
    width:30px;
    height:3px;
    background:white;
}