@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300italic,600,600italic,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300italic,600,600italic,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  background:transparent;
  
}
.header__color{
  background:transparent;
  
}


.projects__grid{
  background: #0F2027;  
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); 
  position:fixed;
  left:0;
  width:100%;
  height: 100%;
  text-align:center;

}
.category__tabs{
  background: #0F2027;  
  background: linear-gradient(to right, #2C5364, #203A43, #0F2027); 
  text-align:center;
  display: flexbox;
  padding-top: 2em;
}
.projects__banner{ 
  display: flex;
}



.social__links{
  
  width: 100%;
  margin:auto;

}

.social__links i{
  color:white;
  font-size: 5em;
  padding:8px;
}

.mdl-layout__drawer-button i {
  color:white;
}
.toolbar{
    background: #282828;
    position:fixed;
    width:100%;
    top:0;
    left:0;
    height:56px;
    overflow: auto;
    z-index: 4;
    

}
.toolbar__container{
    text-align: center;
    width: 100%;
    display:flex;
    flex-direction: row;
    justify-content: center; 
}

.toolbar__navigation{
    display: flex;
    height: 100%;
    align-items: center;
    padding:0 1rem;
    text-align: center;
    width: 100%;
    display:flex;
    flex-direction: row;
    justify-content: center; 


}

.toolbar__logo{
    margin-right: 2rem ;
}
.toolbar__logo span{
    cursor: pointer;
    color:white;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar__navigation-items span{
    cursor: pointer;
    color:white;
    text-decoration: none;
    margin:1rem;

}
.toolbar__navigation-items span:hover,
.toolbar__navigation-items span:active{
    color:orange;
}

.toolbar__links{
    color: white;
    text-decoration: none;
}
.toolbar__links:hover{
    color: orange;
    text-decoration: none;
}


@media (max-width:768px){
    /* .toolbar__navigation-items{
        display: none;
    } */
    .toolbar__links-removable{
        display:none;
    }
    .toolbar__clock{
        display: none;
    }
    
}

@media (min-width:769px){
    .toolbar__toggle-button{
        display: none;
    }
    .toolbar__logo{
        margin-left: 0;
    }
} 

.toolbar__clock{
    float:right;
}
.toggle-button{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    height:20px;
    width:30px;
    background:transparent;
    border:none;
    cursor: pointer;
    padding:0;
    box-sizing: border-box;
}

.toggle-button:focus{
    outline:none;
}
.toggle-button__line {
    width:30px;
    height:3px;
    background:white;
}
