.home__div{
    background:white;
    left:0;
    width:100%;
    height:100%;
    text-align:center;
    z-index: 1;
  }

  .avatar__img{
    width:100%;
    max-width:550px;
    padding-top: 5em;
  }
  
  .banner__text{
    background-color: rgb(71, 71, 71);
    opacity: .8;
    width: 75%;
    margin:auto;
    border-radius: 10px;
    text-align: center;
  }
  
  .banner__text h1{
    font-size: 66px;
    font-weight: bold;
    color:white;
    width:100%;
   
  }
  @media screen and (max-width:600px){
      .banner__text h1{
        font-size:30px;
      }
      .bio__button{
        margin-left:20%;
        margin-right:20%;
      }
      .bio__button p{
        font-size: 10px;
      }
  }
  .banner__text hr{
    border-top: 5px dotted white;
    width:50%;
    margin:auto;
  }
  
  .banner__text p{
    color:white;
    font-size: 20px;
    padding:1em;

  }

  .bio__button:hover{
    color:orange;
    cursor: -webkit-grab;
    cursor: grab;
    

  }
  .bio__button{
    background-color: rgb(71, 71, 71);
    opacity: .8;
    color:white;
    border-radius: 10px;
    text-align: center;
    margin-left: 40%;
    margin-right:40%;
  }
  .bio__button p{
    font-size: 2em;;
  }


.popup-box {
    position: fixed;
    background: #00000050;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
   
  
  .box {
    position: relative;
    width: 70%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #fff;
    border-radius: 4px;
    padding: 20px;
    border: 1px solid #999;
    overflow: auto;
  }
   
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(15% - 30px);
    top: calc(100vh - 85vh - 33px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 20px;
  }
.backdrop{
    position:fixed;
    width:100%;
    height:100%;
    top:0;
    left:0;
    background:rgba(0,0,0,0.3);
    z-index:100;
}
.side-drawer{
    height:100%;
    background:white;
    box-shadow:1px 0px 7px rgba(0, 0, 0, 0.5);
    position:fixed;
    flex-direction: column;
    top:0;
    left:0;
    width:70%;
    max-width: 400px;
    z-index:200;
    transform:translateX(-100%);
    transition:transform 0.3s ease-out; 
}

.side-drawer.open{
    transform:translateX(0);
}

.side-drawer ul{
    height:100%;
    list-style:none;
    display:flex;
    flex-direction: column;
    justify-content: center;

}
.side-drawer li{
    margin:0.5rem;
}
.side-drawer a{
    color:#521751;
    text-decoration: none;
    font-size: 1.2rem;;
}

.side-drawer a:hover,
.side-drawer a:active{
    color: #fa923f;
}

@media(min-width:769px){
    .side-drawer{
        display: none;
    }
}
