@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,300italic,600,600italic,700");

* {
    box-sizing: border-box;
  }
  

  html,
  body {
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: 'avenir next', avenir, -apple-system, BlinkMacSystemFont,
      'helvetica neue', helvetica, ubuntu, roboto, noto, 'segoe ui', arial,
      sans-serif;
    color: #374047;
  }
  
  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  
  body {
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  
  .main {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    width: 100%;
    background-color:white;
  }
  
 .info {
    position: relative;
    z-index: 1;
    color: white;
    text-align: center;
    margin-bottom: 3rem;
    font-size: 14px;
    a {
      color: inherit;
    }
   
    .notice {
      margin-top: 1rem;
      font-weight: 100;
    }
  
    @media screen and (min-width: 980px) {
      font-size: 18px;
      .notice {
        display: none;
      }
    }
  }
  
  .hero {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
  
    background: #0F2027;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  }
  
  .hero-body {
    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    padding: 3rem 1.5rem;
  
    @media screen and (min-width: 980px) {
      padding: 8rem 1.5rem;
    }
  }
  
  .container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .column {
    display: flex;
    flex: 1 1 auto;
    padding: 10px;
    width: 100%;
    @media screen and (min-width: 980px) {
      width: 33.33333333333333%;
    }
  }
  
  .card {
    display: flex;
    flex-direction: column;
    padding: 40px;
    background-color:rgb(184, 184, 184);
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s;
    will-change: transform;
  }
  
  .card-title {
    font-size: 22px;
    font-weight: 600;
    text-align: center;
    padding-top:50px;
  }
  
  .card-body {
    margin-top: 27px;
    margin-bottom: 27px;
    line-height: 1.5;
    font-size: 16px;
    @media screen and (min-width: 576px) {
      font-size: 18px;
    }
  }
  
  .image-container {
    max-width:100%;
    max-height: 100%;
    margin: auto;
    padding-top: 27px;
    padding-bottom: 18px;
    @media screen and (min-width: 576px) {
      padding-bottom: 30px;
    }
  
    
  }


  .image-inner-container {
    width: 100%;
    margin: 0 auto;
    max-width: 250px;
  }
  
  .ratio {
    position: relative;
    width: 100%;
    height: 0;
    overflow: hidden;
  }
  
  .ratio-inner {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  
  .contact__container{
    z-index: 1;
  text-align: center;
    width: 100%;
  }

  .contact__form{
    text-align: center;

  }